<template>
	<div class="after-sales">
		<div class="order-details bottom-space">
			<div class="order-icon"></div>
			<div>
				<p class="order-title">席子</p>
				<p class="order-style">颜色:白色，尺码:L，类型:经典款</p>
			</div>
		</div>
		<div class="cause bottom-space">
			<div class="select-box">
				<div>原因</div>
				<select name="" id="" dir="rtl">
					<option selected="selected" class="hide">请选择</option>
					<option value="1">衣服质量问题</option>
					<option value="2">打印模糊不清</option>
					<option value="3">打印图案不完整</option>
				</select>
			</div>
			<div class="text">
				<p>如非属衣服质量或者打印图案质量问题</p>
				<p>一概不可退货退款</p>
			</div>
		</div>
		<div class="describe">
			<div class="title">问题描述</div>
			<textarea name="" id="" cols="30" rows="10"></textarea>
		</div>
		<div class="file">
			<input class="hide" type="file" accept="image/*">
			<div class="camera"><img src="../assets/ico_camera.png"></div>
		</div>
		<div class="submit">提交</div>
	</div>
</template>

<script>
export default {
	name: 'AfterSales',
	data () {
		return {

		}
	},
	created (){
		document.title="申请售后服务";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {

		}
	}
}
</script>

<style scoped>
	.order-details{
		display: flex;
		display: -webkit-flex;
	}
	.order-icon{
		width: 5rem;
		height: 5rem;
		background: url("../assets/clothes-bg.png") center left no-repeat;background-size: 80% auto;
		flex-shrink:0;
		-webkit-flex-shrink:0;
	}
	.select-box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.order-style{
		color: #999;
	}
	.cause .text{
		color: #999;
	}
	.describe .title{
		color: #666;
	}
</style>



